.header-container {
    min-height      : 60px;
    position        : fixed;
    display         : flex;
    align-items     : center;
    padding         : 0 20px;
    top             : 0;
    left            : 0;
    right           : 0;
    justify-content : space-between;
    z-index         : 1;
    border-bottom   : 1px solid black;
    background-color: rgba(0, 0, 0, 0.2);
}

.center-menu {
    display        : flex;
    align-items    : center;
    justify-content: center;
    flex           : 1;
}

@media screen and (max-width: 768px) {
    div.center-menu {
        display: none;
    }
}

.center-menu>a {
    font-weight : 600;
    padding     : 0 10px;
    flex-wrap   : nowrap;
    margin-right: 10px;
    font        : bold;
    transition  : all ease 0.5s;
}

.center-menu>a :hover {
    background-color: white;
}

.rightMenu {
    display        : flex;
    align-items    : center;
    justify-content: center;
}

.rightMenu>a {
    font-weight : 600;
    padding     : 0 10px;
    flex-wrap   : nowrap;
    margin-right: 10px;
}

.burger-menu-icon {
    cursor: pointer;
}

.burger-nav {
    position        : fixed;
    top             : 0;
    bottom          : 0;
    right           : 0;
    background-color: white;
    width           : 240px;
    z-index         : 5;
    list-style      : none;
    padding         : 20px;
    height          : 550px;
    border-radius   : 20px 0 0 20px;
    transition      : transform 0.3s ease-in;
}

.burger-nav>li {
    padding      : 15px 0;
    border-bottom: 1px solid rgba(0, 0, 0, .2);
}

.burger-nav a {
    font-weight: 600;
}

.close-wrapper {
    display        : flex;
    justify-content: flex-end;
}

.custom-close {
    cursor: pointer;
}