.Wrap {
    height             : 100vh;
    width              : 100vw;
    background-image   : url(../../images/model-s.jpg);
    background-size    : cover;
    background-position: center;
    background-repeat  : no-repeat;
    display            : flex;
    flex-direction     : column;
    justify-content    : space-between;
    text-align         : center;
}

.ItemText {
    padding-top: 15vh;
    text-align : center;
}

.ButtonGroup {
    display        : flex;
    justify-content: center;
    margin         : 20px 0px 100px 20px;
    flex-wrap      : wrap;
}

.LeftButton {
    background-color: rgba(23, 26, 32, 0.8);
    height          : 50px;
    width           : 256px;
    color           : white;
    display         : flex;
    justify-content : center;
    align-items     : center;
    border-radius   : 100px;
    opacity         : 0.85;
    font-size       : 20px;
    cursor          : pointer;
    margin-right    : 8px;
    margin-bottom   : 10px;
}

.RightButton {
    background-color: rgb(252, 252, 253);
    height          : 50px;
    width           : 256px;
    color           : black;
    display         : flex;
    justify-content : center;
    align-items     : center;
    border-radius   : 100px;
    opacity         : 0.65;
    font-size       : 20px;
    cursor          : pointer;
}

.DownArrow {
    cursor   : pointer;
    height   : 40px;
    width    : 40px;
    animation: 2s infinite;
    margin   : 0 auto;
}

.fa-chevron-down {
    color: rgb(7, 6, 6);
}

.arrow {
    width          : 40px;
    height         : 40px;
    background-size: contain;
    animation      : bounce 2s infinite;
}

@keyframes bounce {

    0%,
    20%,
    50%,
    80%,
    100% {
        transform: translateY(0);
    }

    40% {
        transform: translateY(-30px);
    }

    60% {
        transform: translateY(-15px);
    }
}