::-webkit-scrollbar {
    width: 0;
}

#scrollPath {
    position  : fixed;
    top       : 0;
    right     : 0;
    width     : 10px;
    height    : 100%;
    background: rgba(255, 255, 255, 0.05);
}

#progressbar {
    position  : fixed;
    top       : 0;
    right     : 0;
    width     : 10px;
    background: linear-gradient(to top, #05D2DA, #020101);
    animation : animate 5s linear infinite;
}